.App {
  text-align: center;
}

nav {
  min-height: 50px;
  background-color: #333;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav li {
  margin: 1rem 1rem;
  font-size: 1.2rem;
}

li a {
  color: white;
  text-decoration: none;
  padding: 10px;
}

li a:hover {
  background-color: #555;
}
