h1 {
    text-transform: capitalize;
}

.addSong-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.forms-container {
    display: flex;
    gap: 2em;
}

form {
    padding: 10px;
    border: 1px solid darkslategray;
}

form label {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

form input {
    padding: 0.5rem;
    border-radius: 0.25rem;
    border: 1px solid #ccc;
    margin-bottom: 1rem;
    font-size: 1rem;
}

form textarea {
    padding: 0.5rem;
    border-radius: 0.25rem;
    border: 1px solid #ccc;
    margin-bottom: 1rem;
    font-size: 1rem;
    min-width: 40em;
    min-height: 20em;
}

form button {
    margin-top: 2em;
    background-color: #2c8328;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 0.25rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

form button:hover {
    background-color: #0a5905;
}

.submit-btn {
    text-align: center;
}
