.spinner {
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner > div {
    width: 10em;
    height: 10em;
    margin: 2em;
    background: linear-gradient(to bottom, #93cb93, #0a5905);
    border-radius: 100%;
    animation: scale 1s infinite ease-in-out;
    transform-origin: center center;
}


@keyframes scale {
    0% {
        transform: scale(0.7);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.7);
    }
}
