.song-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.song-table {
    border-collapse: collapse;
    width: 80%;
}

.song-table th,
.song-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.song-table th {
    background-color: #f2f2f2;
    color: #444;
    font-weight: bold;
    text-transform: uppercase;
}

.song-table tr:hover {
    background-color: #f5f5f5;
}
