.lyrics-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    border: 1px solid darkslategray;
}

select {
    color: #0a5905;
    border: none;
    padding: 1rem;
    font-size: 1rem;
}

label {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

input {
    padding: 0.5rem;
    border-radius: 0.25rem;
    border: 1px solid #ccc;
    margin-bottom: 1rem;
    font-size: 1rem;
}

button {
    margin-top: 2em;
    background-color: #2c8328;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 0.25rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.submit-btn {
    text-align: center;
}
